	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>Everyone uses an iPhone differently, every user has different apps, needs, and ways they use and interact with their digital content on iOS devices. My goal for my iOS redesign is to address the lack of personalization and give users the ability to customize their phone as they see fit. Next, as I believe Augmented Reality is a technology that will be fundamental to the future of digital interaction, and may one day replace the smartphone, I wanted to incorporate AR into the core of the iOS experience while being intuitive and functional. As a result, this redesign will feature Augmented Reality working together with a constantly learning Siri at the core of the operating system, allowing for new innovative ways to use an iPhone.</p>","frontmatter":{"title":"iOS AR: A complete redesign of iOS","graduate":"Michael Calcada","category":"UI/UX","category2":"Product Design","category3":"Motion Design","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/calcada1-ff26a71c6de3a5ee4660b79f8cb3d93f-a7a62.jpg","srcSet":"/the-next-website/static/calcada1-ff26a71c6de3a5ee4660b79f8cb3d93f-5a1a9.jpg 375w,\n/the-next-website/static/calcada1-ff26a71c6de3a5ee4660b79f8cb3d93f-0908c.jpg 750w,\n/the-next-website/static/calcada1-ff26a71c6de3a5ee4660b79f8cb3d93f-a7a62.jpg 1500w,\n/the-next-website/static/calcada1-ff26a71c6de3a5ee4660b79f8cb3d93f-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/ios-ar-a-complete-redesign-of-ios/","name":"/iOS AR: A complete redesign of iOS/"}}

